import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { AxiosMasterGet } from "../../Axios";
import { getToday, SkeletonLoading } from "../../getDate";
import { renderField, renderFieldSelect } from "../../renderField";
import validasiHeadMutasiBarang from "../../Validations/validasiHeadMutasiBarang";

class HeadMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: false,
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
    };
  }
  componentDidMount() {
    AxiosMasterGet("storages2?tipeData=LOKASI")
      .then((res) =>
        this.setState({
          lokasi: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.change("tanggal", getToday);
    this.props.change("noMutasi", localStorage.getItem("noFaktur"));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 form-group">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Nomor Mutasi"
              type="text"
              name="noMutasi"
              className="form-control"
              readOnly
            />
          </div>
          <div className="col-md-4 form-group">
            <Field
              type="date"
              label="Tanggal"
              id="tanggal"
              name="tanggal"
              component={renderField}
            />
          </div>
          <div className="col-md-4 form-group">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Bon Mutasi"
              type="text"
              id="bonMutasi"
              name="bonMutasi"
            />
          </div>
          <div className="col-md-4 form-group">
            <Field
              id="kategori"
              label="Kategori"
              name="kategori"
              component={renderFieldSelect}
              normalize={(value) => value && value.toUpperCase()}
              options={this.state.listKategori.map((list) => {
                let data = {
                  value: list.nama,
                  name: list.nama,
                };
                return data;
              })}
            ></Field>
          </div>
          <div className="col-md-4 form-group">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Keterangan"
              type="text"
              id="keterangan"
              name="keterangan"
            />
          </div>
          <div className="col-md-4 form-group">
            {this.state.lokasi ? (
              <Field
                label="Lokasi"
                component={renderFieldSelect}
                normalize={(value) => value && value.toUpperCase()}
                type="text"
                id="lokasi"
                name="lokasi"
                onKeyPress={(e) => {
                  e.key === "Enter" && e.preventDefault();
                }}
                options={this.state.lokasi.map((list) => {
                  let data = {
                    value: list.kodeLokasi,
                    name: list.namaLokasi,
                  };
                  return data;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Lokasi" />
            )}
          </div>
          <div className="col-lg-4 col-md-4 mb-3">
            <div className="text-left">
              <button
                className="btn btn-primary"
                disabled={this.props.onSend}
                type="button"
                onClick={this.props.handleSubmit}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                SIMPAN
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadMutasiBarang = reduxForm({
  form: "HeadMutasiBarang",
  enableReinitialize: true,
  validate: validasiHeadMutasiBarang,
})(HeadMutasiBarang);
export default connect()(HeadMutasiBarang);
