import React, { Component, lazy, Suspense } from "react";
import BootstrapTable from "react-bootstrap-table-next";

import { getListMutasiBarang } from "../actions/mutasiBarangAction";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import { reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import ReactTooltip from "react-tooltip";
import {
  finishSend,
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getQty,
  progressSend,
} from "../actions/LogicAction";
import Axios from "axios";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import HeadMutasiBarang from "./Component/HeadMutasiBarang";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";

const FormMutasiBarang = lazy(() => import("./Component/FormMutasiBarang"));
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const expandRow = {
  renderer: (row) => (
    <div>
      <p>{`Tujuan`}</p>
      <p>{`Kode Tujuan : ${row.kodeJenis02} | Keterangan : ${row.keteranganTujuan} | Kat Harga : ${row.kadarTransaksi02} | Kadar : ${row.kadarCetak02} | Pkg(Gr): ${row.pkg02}`}</p>
      <p>{`Bruto(Gr) : ${row.bruto02} | Harga Tujuan : ${row.kadarTransaksi02} | Netto : ${row.netto02}`}</p>
    </div>
  ),
};

const maptostate = (state) => {
  return {
    listBarang: state.provinsi.mutasiBarang,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    sumGross: state.perhitungan.sumGross,
    onSend: state.provinsi.onSend,
  };
};

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class MutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: [],
      kodeJenis: "",
      kodeJenisTujuan: "",
      noMutasi: "",
      kodeLokasi: "",
      tanggal: "",
      keterangan: "",
      kategori: "",
      noBon: "",
      columns: [
        {
          dataField: "Lihat Detail",
          isDummyField: true,
          text: "Lihat Detail",
          formatter: (row) => {
            return (
              <span>
                <i className="fa fa-chevron-down"></i>
              </span>
            );
          },
        },
        {
          dataField: "kodeJenis01",
          text: "Kode Barang",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "kadarTransaksi01",
          text: "Kat Harga",
        },
        {
          dataField: "kadarCetak01",
          text: "Kadar",
        },
        {
          dataField: "pkg01",
          text: "Pkg (Gr)",
        },
        {
          dataField: "bruto01",
          text: "Bruto (Gr)",
        },
        {
          dataField: "kadarTransaksi01",
          text: "Harga",
        },
        {
          dataField: "netto01",
          text: "Netto (Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() =>
                    deleteIndex(
                      "mutasiBarang",
                      rowIndex,
                      this.props.dispatch,
                      getListMutasiBarang()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("EDIT")}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListMutasiBarang());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("mutasiBarang"));
    this.props.dispatch(getNetto("mutasiBarang"));
    this.props.dispatch(getPkg("mutasiBarang"));
    this.props.dispatch(getQty("mutasiBarang"));
    this.props.dispatch(getGross("mutasiBarang"));
  }

  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
    });
    var supp1 = data.kodeBarangTujuan
      ? data.kodeBarangTujuan
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result = supp1.split("|");
    this.setState({
      kodeJenisTujuan: result[0],
    });
    var barang1 = JSON.parse(localStorage.getItem("mutasiBarang")) || [];
    var simpan = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      bruto01: parseFloat(data.bruto).toFixed(3),
      bruto02: parseFloat(data.brutoTujuan).toFixed(3),
      kadarCetak01: parseFloat(data.kadar).toFixed(3),
      kadarCetak02: parseFloat(data.kadarTujuan).toFixed(3),
      kadarTransaksi01: parseFloat(data.kadarTransaksi).toFixed(3),
      kadarTransaksi02: parseFloat(data.kadarTransaksiTujuan).toFixed(3),
      keterangan: data.keterangan,
      keteranganTujuan: data.keteranganTujuan,
      netto01: parseFloat(data.netto).toFixed(3),
      netto02: parseFloat(data.nettoTujuan).toFixed(3),
      pkg01: parseFloat(data.pkg).toFixed(3),
      pkg02: parseFloat(data.pkgTujuan).toFixed(3),
      qty01: 1,
      qty02: 1,
      gross01: (parseFloat(data.bruto) + parseFloat(data.pkg)).toFixed(3),
      gross02: (
        parseFloat(data.brutoTujuan) + parseFloat(data.pkgTujuan)
      ).toFixed(3),
      kodeJenis01: result_explode[0].toUpperCase(),
      kodeJenis02: result[0].toUpperCase(),
      kodeKategori01: "-",
      kodeKategori02: "-",
      k01: 0,
      k02: 0,
      p01: 0,
      p02: 0,
    };
    barang1.push(simpan);
    localStorage.setItem("mutasiBarang", JSON.stringify(barang1));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("FormMutasiBarang")))
      .then(() => this.props.dispatch(getListMutasiBarang()));
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submitMutasiBarang(data) {
    if (localStorage.getItem("mutasiBarang") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var simpan = {
      tanggal: data.tanggal,
      noFaktur: this.props.noFaktur,
      noBon: data.noBon,
      kategoriTransaksi: data.kategori,
      keterangan: data.keterangan,
      kodeLokasi: data.kodeLokasi,
      tipeFaktur: "-",
      barangDetailList: JSON.parse(localStorage.getItem("mutasiBarang")),
      footerItem: {
        totalBruto: 0,
        totalNetto: 0,
        totalQty: 0,
        totalGross: 0,
        totalK: 0,
        totalP: 0,
        totalpkg: 0,
        totalNettoExt: 0,
        totalRp: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("mutasiBarang"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis01,
        ticket.bruto01,
        ticket.kadarTransaksi01,
        ticket.netto01,
        ticket.kodeJenis02,
        ticket.bruto02,
        ticket.kadarTransaksi02,
        ticket.netto02,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Awal",
      "Bruto",
      "Harga (%)",
      "Netto Awal",
      "Kode Tujuan",
      "Harga (%)",
      "Netto Akhir",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "item/dc/mutasibarang", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(reset("HeadMutasiBarang")))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor Bon",
              data.noBon,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "MUTASI BARANG",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("mutasiBarang"))
          .then(() => this.props.dispatch(getListMutasiBarang()))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            this.setState({
              kodeJenis: "",
              kodeJenisTujuan: "",
              noMutasi: "",
              kodeLokasi: "",
              tanggal: "",
              keterangan: "",
              kategori: "",
              noBon: "",
            })
          )
          .then(() => this.forceUpdate())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
    this.props.dispatch(getListMutasiBarang());
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Mutasi Barang" />
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-12">
                    <HeadMutasiBarang
                      onSubmit={(data) => this.submitMutasiBarang(data)}
                      onSend={this.props.onSend}
                      noFaktur={this.props.noFaktur}
                    />
                  </div>
                  <div className="col-lg-4"></div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-center">
                      <button
                        className="btn btn-primary"
                        data-toggle="modal"
                        disabled
                      >
                        BARCODE (+)
                      </button>
                      <button
                        className="btn btn-primary ml-2"
                        data-toggle="modal"
                        disabled
                      >
                        BARCODE (-)
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 mb-3">
                    <div className="text-right">
                      <button
                        className="btn btn-primary"
                        onClick={() => this.props.dispatch(showModal())}
                      >
                        TAMBAH BARANG
                        <i className="fa fa-plus ml-3"></i>
                      </button>
                    </div>
                  </div>
                  <div className="col-lg-12">
                    {this.props.listBarang ? (
                      <BootstrapTable
                        keyField="id"
                        data={this.props.listBarang}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                        expandRow={expandRow}
                      />
                    ) : (
                      <BootstrapTable
                        keyField="id"
                        data={[]}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                        expandRow={expandRow}
                        noDataIndication="Tidak Ada Data"
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              <FormMutasiBarang onSubmit={(data) => this.handleSubmit(data)} />
            </Suspense>
          }
          title="Tambah Data Mutasi Barang"
        />
      </div>
    );
  }
}

export default connect(maptostate, null)(MutasiBarang);
